"use client";
import Image from "next/image";
import { Download } from "lucide-react";
import Link from "next/link";

export default function HeroMinimal() {
  return (
    <section className="relative overflow-hidden rounded-3xl border border-black/5 bg-hero-light p-6 sm:p-10">
      {/* soft blobs */}
      <div className="pointer-events-none absolute -left-24 -top-24 size-[28rem] rounded-full bg-pink-300/20 blur-3xl" />
      <div className="pointer-events-none absolute -right-24 -bottom-20 size-[28rem] rounded-full bg-emerald-300/20 blur-3xl" />

      <div className="grid gap-10 lg:grid-cols-2 items-center">
        {/* SOL: metin */}
        <div>
          <div className="inline-flex items-center gap-2 rounded-full border border-black/10 bg-white/70 px-3 py-1 text-xs text-black/70 backdrop-blur">
            <span>Software Engineer · Data/AI</span>
          </div>

          <h1 className="mt-4 text-4xl md:text-6xl font-extrabold tracking-tight text-black">
            Hasan Memiş
          </h1>
          <p className="mt-2 text-base md:text-lg text-black/70">
            Software Engineer / Product-minded developer. Power BI, SQL, Computer Vision ve
            makine öğrenimi ile iş etkisi yaratan çözümler geliştiriyorum.
          </p>

          <div className="mt-6 flex flex-wrap gap-3">
            <Link href="/cv/Hasan_Memis_CV_TR.pdf" className="inline-flex items-center gap-2 rounded-xl bg-black text-white px-4 py-2 hover:bg-black/90">
              <Download className="h-4 w-4" /> CV (TR)
            </Link>
            <Link href="/cv/Hasan_Memis_CV_EN.pdf" className="inline-flex items-center gap-2 rounded-xl bg-white text-black px-4 py-2 border border-black/10 hover:bg-white/90">
              <Download className="h-4 w-4" /> CV (EN)
            </Link>
          </div>
        </div>

        {/* SAĞ: foto + orbit çizgisi */}
        <div className="relative mx-auto size-[360px] sm:size-[420px]">
          {/* büyük arka daire */}
          <div className="absolute inset-0 rounded-full bg-black/5" />
          {/* orbit çizgileri */}
          <div className="absolute inset-0 animate-orbit">
            <span className="orbit-line orbit-a" />
            <span className="orbit-line orbit-b" />
            <span className="orbit-line orbit-c" />
          </div>
          {/* fotoğraf */}
          <Image
            src="/images/hasan.jpg"      // şimdilik yer tutucu; kendi PNG/JPG’ni koy
            alt="Hasan Memiş"
            fill
            className="rounded-[28px] object-cover object-center shadow-xl ring-1 ring-black/10"
            sizes="(max-width: 768px) 360px, 420px"
          />
          {/* “Come on let's talk” benzeri rozet */}
          <div className="absolute -right-6 top-1/3 grid place-items-center rounded-full border border-emerald-400/40 bg-white/80 px-3 py-2 text-xs text-black/70 backdrop-blur">
            iletişime geç
          </div>
        </div>
      </div>
    </section>
  );
}
